.emergencyIcon {
  font-size: 3rem;
}
.ques-btn {
  border: solid 1px #e3e3e5;
  background-color: #0071e3;
  color: white;
  border-radius: 10px;
}
.ques-btn:hover {
  cursor: pointer;
}
.opt {
  margin: auto;
  max-width: 480px;
  display: flex;
  border-radius: 12px;
  font-size: 1.1rem;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px;
}
.opt:hover {
  cursor: pointer;
  background-color: #efefef;
}
.opt:active {
  box-shadow: 0 0 0 4px rgba(0, 125, 250, 0.6);
}
